:root {
  --color-green-90: #b8efc9;
  --color-blue-90: #d1e4ff;
  --color-red-90: #ffd9d9;
  --color-purple-90: #f0dcee;
  --color-purple-40: #9e2896;
  --color-menubar-background: var(--color-purple-40);
  --color-menubar-font: white;
}

body {
  font-family: sans-serif;
  font-size: medium;
}

nav {
  background-color: var(--color-menubar-background);
  color: var(--color-menubar-font);
}

#menu-occa {
  background-color: var(--color-menubar-background);
}

section {
  padding-bottom: .5rem;
  padding-left: 2rem;
  scroll-margin-top: 4rem;
}

section:target {
  background-color: #fff;
  background-image: linear-gradient(to right, var(--color-purple-40) .5rem, #0000 .5rem);
}

.content {
  max-width: 1200px;
  margin-top: 4rem;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.config label {
  width: 20%;
  display: inline-block;
}

.config input {
  width: 70%;
}

.primaryAction {
  background: var(--color-purple-40);
}

.secondaryAction {
  color: #000;
  background-color: #d3d3d3;
  font-size: .75em;
}

#notificationContainer {
  height: 3rem;
  overflow: hidden;
}

.notification {
  box-sizing: border-box;
  vertical-align: middle;
  animation: 5s forwards cssAnimation;
  display: block;
}

@keyframes cssAnimation {
  0% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.notification .alert {
  margin: .5em;
  padding: 10px;
}

.notification .alertCheckbox, .notification :checked + .alert {
  display: none;
}

.notification .alertText {
  padding: 6px;
}

.notification .alertClose {
  float: right;
}

.notification .success {
  background-color: var(--color-green-90);
}

.notification .info {
  background-color: var(--color-blue-90);
}

.notification .error {
  background-color: var(--color-red-90);
}
